.reviews {
    @include media(m) {
        margin: 40px 0; }

    @include media(d) {
        margin: 40px 110px 20px; }

    &__title-wrap {
        display: flex;
        align-items: center;
        padding-bottom: 10px;
        border-bottom: 1px solid #7A8BA1; }

    &__title {
        font-family: $bold-f;
        color: $dark-c;

        @include media(m) {
            margin-right: 10px;
            font-size: 22px; }

        @include media(d) {
            margin-right: 20px;
            font-size: 36px; } }

    &__counter {
        padding-left: 10px;
        padding-right: 10px;
        font-family: $bold-f;
        color: $light-c;
        letter-spacing: 0.02em;
        text-align: center;
        background-color: $base-c;
        border-radius: 24px;

        @include media(m) {
            min-width: 32px;
            height: 32px;
            line-height: 34px;
            font-size: 18px; }

        @include media(d) {
            min-width: 48px;
            height: 48px;
            line-height: 50px;
            font-size: 36px; } }

    &__btns-wrap {
        display: flex;
        align-items: center;
        justify-content: space-between;

        @include media(m) {
            flex-wrap: wrap;
            justify-content: center; } }

    &__btn {
        @include media(m) {
            margin-top: 32px; }

        @include media(d) {
            margin-top: 40px; } }

    &__more-btn {
        letter-spacing: 0.02em;
        color: $base-c; } }

.reviews-list {
    margin-top: 20px; }

.review {
    &:not(:last-child) {
        margin-bottom: 40px; }

    &__head {
        display: flex;
        align-items: center;
        margin-bottom: 20px; }

    &__date {
        margin-right: 20px;
        font-size: 14px;
        letter-spacing: 0.08em;
        color: $base-gray; }

    &__name {
        font-family: $bold-f;
        font-size: 14px;
        letter-spacing: 0.02em; }

    &__text {
        font-size: 16px;
        line-height: $base-lh; } }
